import React, { useState } from "react";
import axios from "axios";

const LoginForm = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error , setError] = useState('')

  const handleSubmit = async (e) => {
    e.preventDefault();
    const authObject = {
      "Project-ID": "4a857eac-6400-47aa-9bea-337ab1bfafd0",
      "User-Name": username,
      "User-Secret": password,
    };

    try {
        // username /password face un req la chatengine si daca acestea exista primim mesaje
      await axios.get("https://api.chatengine.io/chats" , {headers : authObject})
    //   daca get de mai sus functioneaza
    localStorage.setItem('username' , username)
    localStorage.setItem('password' , password)

    window.location.reload()//facem reload la pagina pentru ca  
    } catch {}
    // daca  avem eroare
    setError('Try again with the right Username / Password')
  };

  return (
    <div className="wrapper">
      <div className="form">
        <h1 className="title">Welcome to ChatAlot</h1>
        <form onSubmit={handleSubmit}>
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            className="input"
            placeholder="username"
            required
          />
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="input"
            placeholder="password"
            required
          />
          <div align="center">
            <button type="submit" className="button">
              <span>Ready to ChatAlot</span>
            </button>
          </div>
         {!error && <h2 className = "error">{error}</h2>}
        </form>
      </div>
    </div>
  );
};

export default LoginForm;
